// 首页商品
import { request } from '@/api/reques.js';
import * as MemberApi from '@/api/member/member.js';
import { goWindow } from '@/api/navigate.js'; // 页面跳转
import config from '@/api/config.js';
export default {
	data() {
		return {
			goodsList: [],
			page_index: 1,
			page_count: 0
		};
	},
	props: {
		options: {
			type: Object,
			default: () => {
				return {};
			}
		}
	},
	created() {
		this.newMyPath(this.page_index);
	},

	methods: {
		// 页面跳转
		handleDetail(goodsId,group,promotion_id = 0){
			let groupId = parseInt(String(group).substr(0,1));
			this.$c.detailsJump(goodsId,groupId,promotion_id)	
		},

		newMyPath(page_index) {
			
			MemberApi.newMyPath({
				page_index: page_index,
			}).then(res => {
				uni.showLoading({
					'title' : '加载中'
				})
				let data = res.data.data;
				for (let index in data) {
					data[index].goods_info.picture_img = config.domain + data[index].goods_info.picture_img;
				}
				if(page_index > 1){
					data = this.shop_list.concat(data)
				}
				setTimeout(()=>{
					this.goodsList = data;
					console.log(this.goodsList)
					this.page_count = res.data.page_count;
					uni.hideLoading()
				},500)
			});
		},
		cancelFavorites(fav_id) {
			let _this = this;
			MemberApi.cancelFavorites({
				fav_id: fav_id,
				fav_type: 'goods'
			}).then(res => {
				console.log(res);
				if (res.data > 0) {
					uni.showToast({
						title: '取消收藏成功',
						duration: 2000,
						success: () => {
							setTimeout(() => {
								_this.newMyPath(1);
							}, 2000);
						}
					});
				} else {
					uni.showToast({
						title: '取消收藏失败',
						icon: 'none',
						duration: 2000,
						success: () => {}
					});
				}
			});
		},
		loadData(e) {
			let _this = this
			if (_this.page_index < _this.page_count) {
				_this.page_index++;
			}else{
				return
			}
			_this.newMyPath(_this.page_index);
		},
		handleErr(e, index) {
			if (e.type == 'error') {
				this.goodsList[index].goods_info.picture_img = '/static/images/list.jpg';
			}
		}
	}
};
